@import url(https://fonts.googleapis.com/css2?family=Zen+Dots&display=swap);
:root {
  --logo-color: #5cdb95;
  --blue-color: #065aaf;
  --white-color:#ffffff;
  --neutral-color: #edf5e1;
  --dark-color:#1a1a1a;
  --semi-grey-color:#a6aaad;
  --grey-color:#4b4949;
  --box-shadow-color:#a8a8a8;
}

 * {
  margin: 0;
  padding: 0;
  border: 0;
  outline: 0;
  box-sizing: border-box;
}
body {
  font-family: sans-serif, "Franklin Gothic Medium", "Arial Narrow",
    Arial;
  color: #a6aaad;
  color: var(--semi-grey-color);
  background-color: #ffffff;
}

/*                utility               */
.center {
  display: -webkit-flex;
  display: flex;
  -webkit-justify-content: center;
          justify-content: center;
  -webkit-align-items: center;
          align-items: center;
}
.d-block{
  display: block;
}
.text-align-center {
  text-align: center;
}
ul {
  list-style: none;
}
a {
  text-decoration: none;
  color: #a6aaad;
  color: var(--semi-grey-color);
  cursor: pointer;
}

.hover:hover {
  color: #05386b;
  border-bottom: 2px solid #065aaf;
  border-bottom: 2px solid var(--blue-color);
}

input{
  display: block;
  background-color: #e9e9e9;
  padding:0 20px;
  border-radius: 5px;
}
input:hover {
  border: 1px solid #a8a8a8;
  border: 1px solid var(--box-shadow-color);
}

/******************************************/


/*         navbar-section         */
#navbar {
  max-width: 1850px;
  min-height: 66px;
  box-shadow: 0 0 12px var(--box-shadow-color);
  padding: 5px 1.5rem;
  margin: auto;
}

/*         navbar-utility         */
.nav-items ul li {
  padding: 0.9rem;
  margin: 0 0.1rem;
  cursor: pointer;
  overflow: hidden;
}

/*         navbar-morpher         */
.logo::before {
  content: " ";
  display: -webkit-flex;
  display: flex;
  -webkit-align-content: center;
          align-content: center;
  -webkit-align-items: center;
          align-items: center;
  -webkit-justify-content: center;
          justify-content: center;
  width: 50px;
  height: 50px;
  margin: auto;
  z-index: 1;
  background-color: #5cdb95;
  background-image: linear-gradient(19deg, #5cdb95 0%, #3fb474 100%);
  border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  -webkit-animation: morphing 10s infinite;
          animation: morphing 10s infinite;
  overflow: hidden;
}
@-webkit-keyframes morphing {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
  }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
  }
}
@keyframes morphing {
  0% {
    border-radius: 30% 70% 70% 30% / 30% 30% 70% 70%;
  }
  25% {
    border-radius: 58% 42% 75% 25% / 76% 46% 54% 24%;
  }
  50% {
    border-radius: 50% 50% 33% 67% / 55% 27% 73% 45%;
  }
  75% {
    border-radius: 33% 67% 58% 42% / 63% 68% 32% 37%;
  }
}

/*         navbar-brand         */
.logo h1 {
  color: var(--logo-color);
  font-family: "Zen Dots", cursive;
  margin-right: 1.3rem;
  cursor: pointer;
}

/*         navbar-search-section         */
.search-box input[type="search"] {
  width: 600px;
  height: 2.5rem;
  background-color: #e9e9e9;
  font-size: 0.9rem;
}

/*         navbar-section list-1 (eg : Home part)         */
/* .list-1 {
  margin-left: auto;
} */

/*         navbar-section-divider ===>  " | "         */
.divider {
  width: 1px;
  height: 30px;
  background-color: var(--dark-color);
}

/*         navbar-section-hamburger         */
.hamburger-icon span {
  display: none;
  font-size: 50px;
  color: var(--logo-color);
  text-shadow: 0px 0px 3px #a8a8a8;
}

/*         navbar-responsive         */

@media (max-width: 1348px) {
  .search-box input[type="search"] {
    max-width: 400px;
  }
}

@media (max-width: 1130px) {
  .nav-items ul li {
    font-size: 0.9rem;
    padding: 0.6rem;
  }
}

@media (max-width: 1045px) {
  #navbar {
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  .search-box {
    display: none;
  }
  .list-1 {
    margin: 0;
  }
  .list-divider {
    display: none;
  }
}

@media (max-width: 700px) {
  #navbar {
    -webkit-justify-content: space-between;
            justify-content: space-between;
    -webkit-flex-direction: column;
            flex-direction: column;
    -webkit-justify-content: space-evenly;
            justify-content: space-evenly;
  }
  .nav-items.responsive{
    -webkit-flex-direction: column;
            flex-direction: column;
    margin: auto;
  }
  .nav-items .list{
    display:none;
    -webkit-flex-direction: column;
            flex-direction: column;
    text-align: center;
  }
  .nav-items .list.show{
    display:block;
  }
  .logo-section{
    width: 100%;
    -webkit-justify-content: space-between;
            justify-content: space-between;
  }
  .logo h1 {
    font-size: 1.3rem;
  }
  .logo::before {
    width: 40px;
    height: 40px;
  }
  .hamburger-icon span {
    display: block;
  }
}

@media (max-width: 300px) {
  #navbar {
    padding: 0 0.5rem;
  }
  .logo::before {
    width: 30px;
    height: 30px;
  }
  .hamburger-icon span {
    font-size: 30px;
  }
}
.switch {
  position: relative;
  display: inline-block;
  width: 45px;
  height: 25px;
}

.switch input { 
  opacity: 0;
  width: 0;
  height: 0;
}

.slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #ccc;
  transition: .4s;
}

.slider:before {
  position: absolute;
  content: "";
  height: 17px;
  width: 17px;
  left: 4px;
  bottom: 4px;
  background-color: white;
  transition: .4s;
}

input:checked + .slider {
  background-color: var(--logo-color);
}

input:focus + .slider {
  box-shadow: 0 0 1px var(--logo-color);
}

input:checked + .slider:before {
  -webkit-transform: translateX(20px);
          transform: translateX(20px);
}

/* Rounded sliders */
.slider.round {
  border-radius: 40px;
}

.slider.round:before {
  border-radius: 50%;
}
