/*          footer-section            */
#footer {
  width: 100%;
  font-family: sans-serif;
  box-shadow: 0 0 6px var(--box-shadow-color);
  padding: 1.5rem 0;
  background: linear-gradient(50deg, #1a1a1a, #131212);
  background-size: 400% 400%;
  -webkit-animation: bg-animate 25s ease infinite;
          animation: bg-animate 25s ease infinite;
}
@-webkit-keyframes bg-animate {
  0% {
    background-position: 0% 83%;
  }
  50% {
    background-position: 100% 18%;
  }
  100% {
    background-position: 0% 83%;
  }
}
@keyframes bg-animate {
  0% {
    background-position: 0% 83%;
  }
  50% {
    background-position: 100% 18%;
  }
  100% {
    background-position: 0% 83%;
  }
}

